.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.about-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.about-row h1 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 43px;
  margin-bottom: -6px;
}

.about-row h4 {
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: 0px;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.my-image {
  height: 400px;
  width: 400px;
}

.column p {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  line-height: 23px;
  margin: 0 0 23px 0;
  text-align: center;
}

.column h5 {
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: -6px;
  margin-top: 0px;
}

.resume-button {
  background-color: black;
  border: none;
  height: 40px;
  margin: auto;
}

.resume-button a {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  text-decoration: none;
  color: white;
  /* font-size: 20px; */
}

.resume-button a:hover {
  color: #3399ff;
}

.column ul {
  list-style-type:circle;
  padding-left: 5px;
  margin-left: 15px;
}

.column li {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  line-height: 23px;
  margin: 0 0 10px 0;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .about-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  .my-image {
    height: 380px;
    width: 380px;
  }

  .about-row h1 {
    font-size: 35px;
    text-align: center;
  }
}
