.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.row h1 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 43px;
  margin-bottom: 20px;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.column h5 {
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  margin-bottom: 10px;
}

.column p {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  text-align: center;
  margin: 0 0 23px 0;
}

.column a {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  color: "#3399ff";
  text-decoration: none;
  margin: 0 0 5px 0;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .column h5 {
    text-align: center;
  }

  .row h1 {
    font-size: 35px;
    text-align: center;
  }
}
