.parent {
  position: fixed;
  top: 0;
  left: 0;
  margin: auto;
  /* max-width: 1100px; */
  width: 100%;
  z-index: 2
}

.navbar {
  display: flex;
  justify-content: space-around;
  background-color: black;
  flex-direction: row;
}

.navbar a {
  font-family: 'Cantarell', sans-serif;
  font-size: 17px;
  text-decoration: none;
  background-color: inherit;
  color: white;
  padding: 20px;
  /* font-family: 'Open Sans'; */
}

.navbar a:hover {
  color: #3399ff;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .navbar {
    display: flex;
    justify-content: center;
    background-color: black;
    flex-direction: row;
  }

  .navbar a {
    font-size: 15px;
    padding: 7px;
    padding-top: 14px;
    padding-bottom: 14px;
  }
}
