.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;
}

.row h1 {
  font-family: 'Fjalla One', sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 43px;
  padding-bottom: 20px;
}

.project-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project-card {
  position: relative;
  height: 450px;
  width: 500px;
  margin: 20px;
}

.project-card img {
  height: inherit;
  width: inherit;
}

.text-block {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
  color: #f1f1f1;
  width: 100%;
  padding: 20px;
}

.text-block:hover + .hide {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}

.hide {
  position: absolute;
  top: 0;
  width: inherit;
  height: inherit;
  background-color: #DCDCDC;
  display: none;
  padding: 20px;
}

.hide li {
  font-family: 'Cantarell', sans-serif;
  font-size: 15px;
  line-height: 23px;
  font-weight: 400;
}

.hide ul {
  margin-left: 0;
  padding-left: 20px;
}

.text-block h3 {
  margin-bottom: -6px;
  margin-top: 10px;
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1px;
  line-height: 23px;
  text-decoration: underline;
}

.text-block h5 {
  margin-bottom: -6px;
  margin-top: 10px;
  font-family: 'Cantarell', sans-serif;
  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: -1px;
  line-height: 23px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .project-card {
    position: relative;
    height: 500px;
    width: 100%;
  }

  .text-block:focus + .hide {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
  }

  .row h1 {
    font-size: 35px;
    text-align: center;
  }
}
